import React from 'react';
import { Box, Tooltip } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Slider from '@material-ui/core/Slider';
import { debounce } from 'lodash';

interface $props {
  values: string[];
  onChange: Function;
  color?: 'secondary'
}

const useStyles = makeStyles({
  root: {
    width: 300,
  },
});

function valuetext(value: number, tagValue=false) {
  if(value < 4){
    const numb = !value ? 1 : value * 3;
    if(tagValue) return 'month-'+numb;
    return `${numb} month old`
  }
  const numb = value-3;
  if(tagValue) return 'age-'+numb;
  return `${numb} year old`
}

const marks = [
  {
    value: 0,
    label: '1m',
  },
  {
    value: 1,
    label: '3m',
  },
  {
    value: 2,
    label: '6m',
  },
  {
    value: 3,
    label: '9m',
  },
  {
    value: 4,
    label: '1y',
  },
  {
    value: 8,
    label: '5y',
  },
  {
    value: 13,
    label: '10y',
  },
];

function convertTagNameToValue(ages: string[]){
  return ages.map((age)=>{
    const [type, n] = age.split('-');
    const numb = parseInt(n);
    if(type === 'month'){
      if(numb === 1) return 0
      return numb / 3
    }
    return numb + 3
  })
}

function ValueLabelComponent(props: {
  children: React.ReactElement;
  open: boolean;
  value: number;
}) {
  const { children, open, value } = props;

  return (
    <Tooltip open={open} enterTouchDelay={0} placement="top" title={valuetext(value)}>
      {children}
    </Tooltip>
  );
}

const defaultValue = [8, 9]

export default function DiscreteSlider({ onChange, values, color}: $props) {
  const [stateValue, setStateValue] = React.useState<number[]>(values.length ? convertTagNameToValue(values) : defaultValue);
  const [debouncedOnChange] = React.useState<Function>(()=>debounce(
    (nextValue: number[])=>{
      onChange(nextValue)
    },
    1000
  ));
  const classes = useStyles();
  return (
    <div className={classes.root}>
      <Box mt={'1rem'}>
        <Typography color={color} gutterBottom>
          Select Your Child's Age Range
        </Typography>
      </Box>
      <Slider
        aria-label="Select your child's age range"
        value={stateValue}
        step={1}
        marks={marks}
        min={0}
        max={13}
        ValueLabelComponent={ValueLabelComponent}
        onChange={(event, nextValue)=>{
          if(typeof nextValue === 'object'){
            setStateValue(nextValue)
            debouncedOnChange(nextValue.map((v)=>valuetext(v, true)))
          }
        }}
      />
      {
        stateValue === defaultValue ? null : <Box mt={'1rem'}>
          <Typography variant="body2"><strong>Age Range: </strong>
            {`${valuetext(stateValue[0])} to ${valuetext(stateValue[1])}`}
          </Typography>
        </Box>
      }
      
    </div>
  );
}