import React, { useState, useEffect } from 'react';
import { Typography, Divider, makeStyles } from '@material-ui/core';
import classnames from 'classnames';
import { blue } from '@material-ui/core/colors';
import { useCustomSwr, SERVER_ADDRESS, get } from '../../../../utils/remote';
import SheduleRow from '../../../components/general/SheduleRow';

const useStyles = makeStyles({
  header: {
    marginTop: '1rem',
    color: blue[700]
  }
})

interface $props {
  tag: string;
  ages: string[];
  portraitFeatured?: Boolean;
  activitiesLoadedCallback?: Function;
  first?: Boolean;
}

const defaultValue: any[] = [];

export default function SearchActivitiesNonSearchRow({tag, ages, portraitFeatured = false, activitiesLoadedCallback, first}: $props){
  const classes = useStyles();
  const [ previousSizes, setPreviousSizes ] = useState({});
  const [ previousTags, setPreviousTags ] = useState(new Set());
  const defaultTags =  ages;
  
  const tags = defaultTags.concat([tag]).map((t: string)=>'#'+t).reverse().join(' ');
  const { data, page } = useCustomSwr(SERVER_ADDRESS+`/shedules/activities/public`, { 
    defaultValue,
    page: true,
    query: {
      tags,
      portraitFeatured
    }, 
    deps: [],
    cache: true,
    callback: activitiesLoadedCallback
  });
  useEffect(()=>{
    setPreviousSizes({
      ...previousSizes,
      [page]: data.length
    });
  }, [data]);
  return <div>
    <Typography align="center" variant="h6" component="h3" className={classnames(classes.header, 'shedule-row-title')}>{'#'+tag}</Typography>
    {
      !data.length ? <p>No Results</p> : <SheduleRow first={first} data={data} />
    }
    <Divider variant="inset" />
  </div>
}