import React, { useEffect, useState } from 'react';
import _ from 'lodash';
import LazyLoad from 'react-lazyload';

import SearchActivitiesNonSearchRow from '../NonSearchRow';
import SearchActivitiesNonSearchPortraitRow from '../NonSearchPortraitRow';
import SearchActivitiesNonSearchSquareRow from '../NonSearchSquareRow';

import { useCustomSwr, SERVER_ADDRESS } from '../../../../utils/remote';


export default function ListActivities({ages}: {subjects: string[], ages: string[]}){
  const [tagOrder, setTagOrder] = useState<string[]>([]);
  const { data: popularTags } : {data: string[]} = useCustomSwr(SERVER_ADDRESS+'/tags/popular', {
    cache: true,
    defaultValue: []
  });
  useEffect(()=>{
    setTagOrder(
      ([] as any).concat(_.shuffle(popularTags||[]).filter((tag: string)=>(tag.indexOf('age') === -1)))
    );
  }, [ages, popularTags])

  const squareRender = (tag: string)=>{
    return <div key={tag} style={{minHeight: '300px', minWidth: '100%'}}><LazyLoad height={200} >
      <SearchActivitiesNonSearchSquareRow tag={tag} ages={ages} />
    </LazyLoad></div>;
  }

  const portraitRender = (tag: string)=>{
    return <div key={tag} style={{minHeight: '300px', minWidth: '100%'}}><LazyLoad height={200} >
      <SearchActivitiesNonSearchPortraitRow tag={tag} ages={ages} />
    </LazyLoad></div>;
  }
  
  const render = (tag: string, index: number)=>{
    return <div key={tag} style={{minHeight: '300px', minWidth: '100%'}}><LazyLoad height={300}>
      <SearchActivitiesNonSearchRow first={index === 0} tag={tag} ages={ages} />
    </LazyLoad></div>;
  }
  return <div>
    {
      tagOrder.map((tag: string, i: number)=>{
        if(i === 1 || i === 8) return squareRender(tag);
        if(i === 3 || i === 6 || i === 12) return portraitRender(tag)
        return render(tag, i)
      })
    }
  </div>
}