import React, { useState } from 'react';
import { Button, Dialog, DialogActions, DialogContent, Typography } from '@material-ui/core';
import { $sendEventProps, sendEvent } from '../../../../utils/ga';



export default function ButtonPrompt({ga, raw, children, buttonProps, message, onConfirm}: {ga?: $sendEventProps, raw?: Boolean, children: JSX.Element | string, buttonProps: Object, message: string | JSX.Element, onConfirm: Function}){
  const [ isOpen, setIsOpen ] = useState(false);
  return <>
    <Button 
      {...(buttonProps || {})}
      onClick={()=>{
        setIsOpen(true)
        if(ga){
          sendEvent(ga)
        }
      }}
      // onClose={()=>setIsOpen(false)}
    >
      {
        children
      }
    </Button>
    <Dialog open={isOpen}>
      <DialogContent>
        {
          raw ? message : <Typography variant="body1">{message}</Typography>
        }
      </DialogContent>
      <DialogActions>
        <Button color="secondary" onClick={()=>{
          onConfirm()
          setIsOpen(false);
        }}>Confirm</Button>
        <Button onClick={()=>setIsOpen(false)}>Cancel</Button>
      </DialogActions>
    </Dialog>
    </>
}