import { Button, Card, CardContent, Typography } from '@material-ui/core';
import React from 'react';
import { userSelector } from '../../../../utils/redux/user';
import { useSelector } from 'react-redux';
import { setupIntents } from '../../../../utils/stripe';
import { sendEvent } from '../../../../utils/ga';



export default function UserCreateError(){
  const user = useSelector(userSelector);
  const handleVerifyPayment = ()=>{
    sendEvent({category: 'payment', action: 'click', label: 'verify'});
    setupIntents()
  }
  return <div>
    <Card>
      <CardContent>
        <Typography>To complete the signup process, please provide payment information by clicking the button below</Typography>
        <Button onClick={handleVerifyPayment}>Proceed to payment section</Button>
      </CardContent>
    </Card>
  </div>
}