import React from 'react';
import ReactJoyride, { CallBackProps, STATUS, ACTIONS, EVENTS } from 'react-joyride';

interface $props {
  onEnd?: Function;
  onChange?: Function;
  joyrideProps: any;
  step?: number;
}

export default function Tour({onChange, onEnd, joyrideProps, step}: $props){
  const [lastCheck, setLastCheck] = React.useState(()=>new Date().getTime());
  const [stepIndex, setStepIndex] = React.useState(()=>(step || 0));
  console.log('stepIndex', stepIndex);
  const handleCallback = (data: CallBackProps)=>{
    console.log('data', data);
    const { action, /*index,*/ type, status } = data;
    let nextStepIndex = 0;
    if(([EVENTS.STEP_AFTER, EVENTS.TARGET_NOT_FOUND] as string[]).includes(type)){
      nextStepIndex = (action === ACTIONS.PREV ? -1 : 1) + stepIndex;
      const currentTime = new Date().getTime();
      if(currentTime - lastCheck > 400){
        onChange && onChange(nextStepIndex);
        setStepIndex(nextStepIndex);
        setLastCheck(currentTime);
      } else {
        setStepIndex(stepIndex);
      }
    }
    if (([STATUS.FINISHED, STATUS.SKIPPED] as string[]).includes(status)) {
      nextStepIndex = joyrideProps.steps.length;
      onEnd && onEnd(nextStepIndex);
      setStepIndex(nextStepIndex);
    }
  }
  return <ReactJoyride
    {...joyrideProps}
    stepIndex={stepIndex}
    callback={handleCallback}
  />
}