import React from 'react';
import _, { debounce } from 'lodash';
import { Button } from '@material-ui/core';
import { useSelector, useDispatch } from 'react-redux';
import { v4 } from 'uuid';
import {
    createConfigAction,
    dashboardTabsSelector
} from '../../../../utils/redux/config';

import { createUIAction, uiSelector } from '../../../../utils/redux/ui';
import DashboardTabForm from './DashboardTabForm';
import { Box } from '@material-ui/core';
import { sendEvent } from '../../../../utils/ga';


interface $props {
  ordinal: number,
};

export default function Config({ordinal}: $props){
  const dashboardTabs = useSelector(dashboardTabsSelector);
  const { dashboard } = useSelector(uiSelector);
  const dispatch = useDispatch();
  const dashboardTab = dashboardTabs.find(x=>x.ordinal===ordinal);
  const onChange = (key: string, value: any)=>{
    dispatch(createConfigAction({dashboardTabs: [
      {
        ...dashboardTab,
        [key]: value,
        touched: true,
      },
     ...dashboardTabs.filter(x=>x.ordinal !== ordinal),
     ...(dashboardTab && !dashboardTab.touched ? [{
       ordinal: dashboardTabs.length,
       ages: [],
       subjects: [],
       uuid: v4(),
     }] : []),
    ]}))
  }
  const debouncedOnChange = debounce((value)=>{
    onChange('name', value)
  }, 500)

  if(!dashboardTab) return null;
  return <div>
    <DashboardTabForm
      key={dashboardTab.uuid}
      onChange={onChange}
      ordinal={ordinal}
      dashboardTab={dashboardTab}
      debouncedOnChange={debouncedOnChange}
    />
    <Box m="1rem">
    {
      !dashboardTab.ages.length ? null : <Button size="large" variant="contained"
      color="primary" onClick={()=>{
        sendEvent({category: 'dashboard', action: 'click', label: 'showActivities'});
        dispatch(createUIAction({
          dashboard: {
            ...dashboard,
            isEditing: false
          }
        }))
      }}>Show Activities</Button>
    }
    {
      dashboardTabs.length === 1 || !dashboardTab.touched ? null : <Button onClick={()=>{
        sendEvent({category: 'dashboard', action: 'click', label: 'deleteTab'});
        dispatch(createConfigAction({
          dashboardTabs: [
            ...dashboardTabs.filter(x=>x.ordinal !== ordinal).map(x=>({...x, ordinal: x.ordinal > ordinal ? x.ordinal-1 : x.ordinal}))
          ]
        }))
        dispatch(createUIAction({
          dashboard: {
            ...dashboard,
            isEditing: false
          }
        }))
      }}
      color="secondary" size="small">Delete</Button>
    }
    </Box>
  </div>
}
