import React from 'react';
import Tour from '../../../Tour';
import { useDispatch, useSelector } from 'react-redux';
import {
    onboardingSelector,
    createConfigAction,
    addedActivitiesSelector
} from '../../../../../../utils/redux/config';
import { Typography } from '@material-ui/core';
import Beacon from '../../../Tour/Beacon';
import { Popper } from '@material-ui/core';

// const usePopoverStyles = makeStyles({
//   paper: {
//     background: 'none',
//     boxShadow: 'none'
//   },
// })

export default function SlotTour(props: any){
  const dispatch = useDispatch ();
  const [bookmarkNav, setBookmarkNav] = React.useState<null|Element>(null);
  const [bookmarkSpan, setBookmarkSpan] = React.useState<null|Element>(null);
  // const popoverClasses = usePopoverStyles();
  const { completed, step} = useSelector(onboardingSelector('listActivities'));
  const addedActivities= useSelector(addedActivitiesSelector);
  React.useEffect(()=>{
    if(step === steps.length && Object.keys(addedActivities).length && !completed){
      dispatch(createConfigAction({
        onboarding: {
          listActivities: {
            completed: false,
            step: step + 1,
          }
        }
      }))
    }
  }, [addedActivities])
  /**
   * To be removed
   */
  // React.useEffect(()=>{
  //   dispatch(createConfigAction({
  //     onboarding: {
  //       listActivities: {
  //         completed: false,
  //         step: 8,
  //       }
  //     }
  //   }))
  // }, [])
  /**
   * 
   */
  React.useEffect(()=>{
    setBookmarkNav(document.getElementsByClassName('go-to-bookmarks')[0]);
    setBookmarkSpan(document.querySelectorAll('.activity-card .add-to-bookmarks')[0]);
  }, [])
  
  return <>
    {
      (step >= steps.length || completed) ? null : <Tour
      step={step}
      joyrideProps={
        {
          continuous: true,
          run: true,
          scrollToFirstStep: true,
          showProgress: true,
          showSkipButton: false,
          // disableOverlayClose: true
          // disableCloseOnEsc: true,
          styles: {
            options: {
              zIndex: 10000,
            }
          },
          steps,
        }
      }
      onChange={(nextStep: number)=>{
        dispatch(createConfigAction({
          onboarding: {
            listActivities: {
              completed: false,
              step: nextStep,
            }
          }
        }))
      }}
      onEnd={(nextStep: number)=>{
        dispatch(
          createConfigAction({
            onboarding: {
              listActivities: {
                completed: false,
                step: nextStep,
              }
            }
          }))
        }
      }
      />
    }
    {
      // (completed || step < 6) ? null : <Tour
      // step={step}
      // joyrideProps={
      //   {
      //     continuous: true,
      //     run: true,
      //     scrollToFirstStep: true,
      //     showProgress: false,
      //     showSkipButton: false,
      //     // disableOverlayClose: true
      //     // disableCloseOnEsc: true,
      //     styles: {
      //       options: {
      //         zIndex: 10000,
      //       }
      //     },
      //     steps: bookmarkSteps,
      //   }
      // }
      // onChange={(nextStep: number)=>{
      //   dispatch(createConfigAction({
      //     onboarding: {
      //       listActivities: {
      //         completed: false,
      //         step: nextStep,
      //       }
      //     }
      //   }))
      // }}
      // onEnd={(nextStep: number)=>{
      //   dispatch(
      //     createConfigAction({
      //       onboarding: {
      //         listActivities: {
      //           completed: true,
      //           step: nextStep,
      //         }
      //       }
      //     }))
      //   }
      // }
      // />
    }
    <Popper
      open={Boolean(bookmarkSpan) && step === steps.length && !completed}
      anchorEl={bookmarkSpan}
      style={{ zIndex: 3, pointerEvents: 'none', marginTop: '-20px' }}
    >
      <Beacon style={{}} />
    </Popper>
    <Popper
      open={Boolean(bookmarkNav) && step === (steps.length + 1) && !completed}
      anchorEl={bookmarkNav}
      style={{ zIndex: 10000, pointerEvents: 'none', marginLeft: '10px', marginTop: '-15px' }}
    >
      <Beacon style={{}} />
    </Popper>
  </>
}

