import React, { useEffect, useRef, useState } from 'react';
import {v4} from 'uuid';
import Swiper from 'swiper';
import 'swiper/swiper-bundle.css';
import "./index.css";

import Slides from './Slides';
import useWindowDimensions from '../../../../utils/window';
import classNames from 'classnames';
import { isMobile } from 'react-device-detect';
import { sendEvent } from '../../../../utils/ga';

interface $props {
  renderSlides: ()=>JSX.Element[],
  slideWidth?: number
}
const letters = 'abcdefghijklmnop';

const getIncrementAmount = (width: number, slideWidth: number)=>Math.max(Math.floor(width/slideWidth),1)

export default function Scroll({renderSlides, slideWidth = 300}: $props){
  const [swiper, setSwiper] = useState<any>(null);
  const [onEnd, setOnEnd] = useState(false);
  const [onStart, setOnStart] = useState(true);
  const [id] = useState(v4().replace(/-/g, '0').replace(/(\d)/g, (m, p)=>letters[Number(p)]));
  const {width} = useWindowDimensions();
  const renderedSlides = renderSlides();
  const buttonStyle = isMobile ? {
    opacity: 1
  } :  {};
  useEffect(()=>{
    const sw = new Swiper('.'+id, {
      slidesPerView: 'auto',
      on: {
        reachEnd: ()=>setOnEnd(true),
        reachBeginning: ()=>setOnStart(true),
      }
    });
    setSwiper(sw);
  }, [id])
  return <div className={classNames("swiper-container", id)}>
    <Slides>
    {
      renderSlides()//.map((slide, i) => <ScrollSlide key={i}>{}</ScrollSlide>)
    }
    </Slides>
    {
      onEnd ? null : 
      <div
        style={buttonStyle}
        onClick={()=>{
          if(swiper && swiper.slideNext){
            sendEvent({category: 'scroller', action: 'click', label: 'next', value: swiper.activeIndex});
            if(width > 500){
              const indexIncrementCount = getIncrementAmount(width, slideWidth);
              const nextIndex = Math.min(swiper.activeIndex + indexIncrementCount, renderedSlides.length-1);
              swiper.slideTo(nextIndex, 300)
            } else {
              swiper.slideNext(300);
            }
            setOnStart(false)
          }
          
        }} className={classNames('swiper-button', "swiper-button-next")} />
    }
    {
      onStart ? null : 
      <div
      style={buttonStyle}
      onClick={()=>{
          if(swiper && swiper.slideNext){
            sendEvent({category: 'scroller', action: 'click', label: 'prev', value: swiper.activeIndex});
            if(width > 500){
              const indexDecrementCount = Math.max(Math.floor(width/slideWidth),1);
              const nextIndex = Math.max(swiper.activeIndex - indexDecrementCount, 0);
              swiper.slideTo(nextIndex, 300)
            } else {
              swiper.slidePrev(300);
            }
            setOnEnd(false)
          }
      }} className={classNames('swiper-button', "swiper-button-prev")} />
    }
  </div>
}